import React from 'react';
import Tilt from 'react-tilt'
import logo from './logo.png'


const Logo = () => {
	return(
		<Tilt className="Tilt pa3 ma4 br2 shadow-2 pill" options={{ max : 40 }} style={{ height: 150, width: 150 }} >
 			<div className="Tilt-inner"> 
 				<img alt='Logo' src={logo}/> 
 			</div>
		</Tilt>
	)
}

export default Logo;