import React from 'react';

const Navigation = ({onRouteChange, isSignedIn}) => {
	
		if(isSignedIn) {
			return(
				<nav style = {{display : 'flex', justifyContent : 'flex-end'}}>
					<p onClick= {() => onRouteChange('signout')} className = 'f4 underline dim pointer link pa3'>Sign out</p>
				</nav>
			);
		}else{
			return(
				<nav style = {{display : 'flex', justifyContent : 'flex-end'}}>
					<p onClick= {() => onRouteChange('signin')} className = 'f4 underline dim pointer link pa3'>Sign in</p>
					<p onClick= {() => onRouteChange('register')} className = 'f4 underline dim pointer link pa3'>Register</p>

				</nav>
				);
		}

	
}

export default Navigation;