import React, { Component } from 'react';
import './App.css';
import Navigation from './components/navigation/Navigation';
import Logo from './components/logo/Logo';
import ImageLinkForm from './components/imageLinkForm/ImageLinkForm';
import 'tachyons';
import './App.css';
import FaceRecognition from './components/facerecognition/FaceRecognition';
import Particles from 'react-particles-js';
import Clarifai from 'clarifai';
import SignIn from './components/signIn/SignIn';
import Register from './components/Register/Register'

const app = new Clarifai.App({
  apiKey: '400004d805b4415985aa8195c2558864'
});


const particlesParam = {
  particles: {
    number :{
      value : 80,
      density :{
        enable : true,
        value_area : 800
      }
    }
  }
};

class App extends Component{
  constructor(){
    super();
    this.state = {
      input : '',
      imageUrl: '',
      box:{},
      route: 'signin',
      isSignedIn: false
    }
  }

  calculateFaceLocation = (data) => {
    const clarifayFace = data.outputs[0].data.regions[0].region_info.bounding_box;
    const img = document.getElementById('inputImage');
    const width = Number(img.width);
    const height = Number(img.height);
    return{
      leftCol: clarifayFace.left_col * width,
      topRow: clarifayFace.top_row * height,
      rightCol: width - (clarifayFace.right_col * width),
      bottomRow: height - (clarifayFace.bottom_row * height)
    }
  }

  displayFaceBox = (box) =>{
    console.log(box)
    this.setState({box: box});
  }

  onInputChange = (event) =>{
      this.setState({input:event.target.value})
  }

  onButtonSubmit = () =>{
      this.setState({imageUrl : this.state.input})
      app.models
      .predict(
      Clarifai.FACE_DETECT_MODEL,
          this.state.input
      )
      .then(response =>  this.displayFaceBox(this.calculateFaceLocation(response))
      
      )
      .catch(err=>console.log(err))
  }

  onRouteChange = (route) =>{
    if(route === 'signout'){
      this.setState({isSignedIn: false})
    }
    else if (route === 'home'){
      this.setState({isSignedIn: true})
    }
    this.setState({route: route})
  }
  render(){
   const { isSignedIn, imageUrl, route, box } = this.state;
    return(
      <div>
        <Particles className = 'particles' params={particlesParam}/>
          <div>
            <Navigation isSignedIn={isSignedIn} onRouteChange={this.onRouteChange}/>
            {route === 'home' 
              ?
              <div>
                <Logo/>
                <ImageLinkForm 
                  onInputChange={this.onInputChange} 
                  onButtonSubmit={this.onButtonSubmit}
                />
                <FaceRecognition box={box} imageUrl={imageUrl} />
              </div>
              : (
                  route === 'signin' 
                  ?  <SignIn onRouteChange={this.onRouteChange}/>
                  :  <Register onRouteChange={this.onRouteChange}/>
                )
            }
          </div>
        </div>
    )
  }
}


export default App;
